___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 650px) {
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
  
    .app-bar .app-bar__brand h1 {
      font-size: 1.5em;
    }
  
    .app-bar .app-bar__menu {
      display: none;
    }
  
    .app-bar .app-bar__nav {
      position: static;
      width: 100%;
    }
  
    .app-bar .app-bar__nav ul li {
      display: inline-block;
    }
  
    .app-bar .app-bar__nav ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
    }
  }
  
  @media screen and (max-width: 500px) {
  
    .app-bar .app-bar__title h1 a{
      font-size: 20px;
      align-items: center;
    }
  
    .headerinner {
      display: grid;
      grid-template-columns: 1fr 1fr;
      padding: 12px 25px;
      margin-top: 0;
    }
    .nav-mob {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 15px;
      list-style-type: none;
    }
    .headertitle {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-size: 24px;
    }
    .menu_drawer {
      line-height: 44px;
      margin-top: 0px;
      color: #fff;
      text-decoration: none;
      background-color: transparent;
      border: none;
  
    }
    .nav {
      z-index: 10;
      background-color: #753449;
      width: 100%;
      position: absolute;
      -webkit-transform: translate(100%, 0);
      transform: translate(-100%, 0);
      transition: transform 0.3s ease;
      top: 92px;
    }
    .open {
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
    }
    .navitem {
      display: list-item;
      border-bottom: 1px solid #ffc4c4;
      width: 100%;
      text-align: left;
    }
  
    .resto_menu {
      display: flex;
      grid-template-columns: auto auto;
      gap: 20px;
      margin: 15px auto;
      padding: 15px 0px;
      border-top: 1px solid rgb(133, 14, 53);
      border-bottom: 1px solid rgb(133, 14, 53);
      flex-direction: column;
    }
  
    .menuContainer{
      display: block;
     }
    }
  @media screen and (min-width: 500px) {
    .nav-mob {
      display: none;
    }
    .info_judul {
      font-size: 18px;
    }
    .info_city {
      font-size: 16px;
    }
    .info_desc {
      font-size: 13px;
    }
  }
  
  @media screen and (min-width: 650px) {
    .content {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: 10px;
      grid-row-gap: 15px;
    }
  }
  
  @media screen and (min-width: 950px) {
    .content {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .hero {
      min-width: 1000px;
    }
    main {
      max-width: 1200px;
    }
  } `, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;IACI;MACE,+BAA+B;MAC/B,iBAAiB;IACnB;;IAEA;MACE,gBAAgB;IAClB;;IAEA;MACE,aAAa;IACf;;IAEA;MACE,gBAAgB;MAChB,WAAW;IACb;;IAEA;MACE,qBAAqB;IACvB;;IAEA;MACE,qBAAqB;MACrB,YAAY;MACZ,kBAAkB;MAClB,SAAS;IACX;EACF;;EAEA;;IAEE;MACE,eAAe;MACf,mBAAmB;IACrB;;IAEA;MACE,aAAa;MACb,8BAA8B;MAC9B,kBAAkB;MAClB,aAAa;IACf;IACA;MACE,aAAa;MACb,mBAAmB;MACnB,2BAA2B;MAC3B,gBAAgB;MAChB,qBAAqB;IACvB;IACA;MACE,aAAa;MACb,yBAAyB;MACzB,mBAAmB;MACnB,eAAe;IACjB;IACA;MACE,iBAAiB;MACjB,eAAe;MACf,WAAW;MACX,qBAAqB;MACrB,6BAA6B;MAC7B,YAAY;;IAEd;IACA;MACE,WAAW;MACX,yBAAyB;MACzB,WAAW;MACX,kBAAkB;MAClB,qCAAqC;MACrC,8BAA8B;MAC9B,+BAA+B;MAC/B,SAAS;IACX;IACA;MACE,kCAAkC;MAClC,0BAA0B;IAC5B;IACA;MACE,kBAAkB;MAClB,gCAAgC;MAChC,WAAW;MACX,gBAAgB;IAClB;;IAEA;MACE,aAAa;MACb,gCAAgC;MAChC,SAAS;MACT,iBAAiB;MACjB,iBAAiB;MACjB,sCAAsC;MACtC,yCAAyC;MACzC,sBAAsB;IACxB;;IAEA;MACE,cAAc;KACf;IACD;EACF;IACE;MACE,aAAa;IACf;IACA;MACE,eAAe;IACjB;IACA;MACE,eAAe;IACjB;IACA;MACE,eAAe;IACjB;EACF;;EAEA;IACE;MACE,aAAa;MACb,8BAA8B;MAC9B,qBAAqB;MACrB,kBAAkB;IACpB;EACF;;EAEA;IACE;MACE,aAAa;MACb,kCAAkC;IACpC;EACF;;EAEA;IACE;MACE,iBAAiB;IACnB;IACA;MACE,iBAAiB;IACnB;EACF","sourcesContent":["@media screen and (min-width: 650px) {\r\n    .app-bar {\r\n      grid-template-columns: 1fr auto;\r\n      padding: 8px 32px;\r\n    }\r\n  \r\n    .app-bar .app-bar__brand h1 {\r\n      font-size: 1.5em;\r\n    }\r\n  \r\n    .app-bar .app-bar__menu {\r\n      display: none;\r\n    }\r\n  \r\n    .app-bar .app-bar__nav {\r\n      position: static;\r\n      width: 100%;\r\n    }\r\n  \r\n    .app-bar .app-bar__nav ul li {\r\n      display: inline-block;\r\n    }\r\n  \r\n    .app-bar .app-bar__nav ul li a {\r\n      display: inline-block;\r\n      width: 120px;\r\n      text-align: center;\r\n      margin: 0;\r\n    }\r\n  }\r\n  \r\n  @media screen and (max-width: 500px) {\r\n  \r\n    .app-bar .app-bar__title h1 a{\r\n      font-size: 20px;\r\n      align-it